export const NewUserFollowView = ({userId, content}) => {
  const classes = useStyles();
  const [userInfo, setUserInfo] = useState(null);
  const history = useHistory();
  const api = Global.GetPortal().getAppService();

  useEffect(() => {
    const fetchUserInfo = async () => {
      setUserInfo(null);
      try {
        const res = await api.fetchUserInfo(userId);
        setUserInfo(res);
        return true;
      } catch (err) {
        console.log(err);
        history.push("/account-doesnt-exist");
      }
    };

    (async () => {
      await fetchUserInfo();
    })();
  }, [userId]);

  let bodyComp = null;
  if (userInfo) {
    const profileName = userInfo.getNickname(true);
    const profileOriginalUsername =
      userInfo.getOriginalUsername() || userInfo.getUsername();
    const token = api.portal.getUserToken();
    const currentUserId = api.getUserId();

    // const getTotalFollowers = () => {
    //   let flg = userInfo?.data?.flg ? userInfo?.data?.flg : 0;
    //   let twitter = userInfo?.data?.twt_flg ? userInfo?.data?.twt_flg : 0;
    //   return shortNum(flg * 1 + twitter * 1);
    // };

    bodyComp = (
      <>
        <GBackTitle
          title={profileName}
          subTitle={`@${profileOriginalUsername}`}
        />
        {/* <span>
            {content === "FOLLOWING"
              ? `${shortNum(userStats.getFollowsCount(0))} ${t(
                "getter_fe.profile.common.following",
              )}`
              : getTotalFollowers() + t(
                "getter_fe.profile.common.followers",
              )}
          </span> */}

        <FollowFeed
          userId={userId}
          currentUserId={currentUserId}
          userInfo={userInfo}
          content={content}
          token={token}
        />
      </>
    );
  }

  return <div className={classes.leftDashBoardWrapper}>{bodyComp}</div>;
};
