import {Helmet} from "react-helmet";
import {useLocation} from "react-router";
import VisualConsts from "src/core/model/VisualConsts";
import Global from "src/system/Global";
import AppConsts from "../AppConsts";

const DEFAULT_TITLE = AppConsts.APP_WEB_NAME;
const DEFAULT_DESCRIPTION = "Web Page of: " + DEFAULT_TITLE;
const DEFAULT_TWITTER = AppConsts.APP_TWITTER_USERNAME;
const DEFAULT_SEPERATOR = " | ";
const DEFAULT_IMAGE = Global.GetAppLogoURL();
const FB_APP_ID = Global.GetFacebookAppID();

export const NewUIPage = ({children, title, description, noCrawl}) => {
  const location = useLocation();
  const SITE_URL = Global.GetSiteURL();

  const theTitle = title ? title.substring(0, 60) : DEFAULT_TITLE;
  const theDescription = description
    ? description.substring(0, 155)
    : DEFAULT_DESCRIPTION;

  const metaTags = [
    {itemprop: "name", content: theTitle},
    {
      itemprop: "description",
      content: theDescription,
    },
    {itemprop: "image", content: DEFAULT_IMAGE},
    {
      name: "description",
      content: theDescription,
    },
    {name: "twitter:card", content: "summary_large_image"},
    {name: "twitter:site", content: DEFAULT_TWITTER},
    {name: "twitter:creator", content: DEFAULT_TWITTER},
    {property: "og:title", content: theTitle},
    {property: "og:type", content: "website"},
    {property: "og:url", content: SITE_URL + location.pathname},
    {property: "og:image", content: DEFAULT_IMAGE},
    {
      property: "og:image:width",
      content: `${VisualConsts.DEFAULT_THUMBNAIL_WIDTH}`,
    },
    {
      property: "og:image:height",
      content: `${VisualConsts.DEFAULT_THUMBNAIL_HEIGHT}`,
    },
    {property: "og:description", content: theDescription},
    {property: "og:site_name", content: DEFAULT_TITLE},
    {property: "fb:app_id", content: FB_APP_ID},
  ];

  if (noCrawl) {
    metaTags.push({name: "robots", content: "noindex, nofollow"});
  }

  return (
    <>
      <Helmet
        htmlAttributes={{
          lang: "en",
          itemscope: undefined,
          itemtype: "http://schema.org/WebPage",
        }}
        title={
          title ? `${title}${DEFAULT_SEPERATOR}${DEFAULT_TITLE}` : DEFAULT_TITLE
        }
        link={[
          {
            rel: "canonical",
            href: SITE_URL + location.pathname,
          },
        ]}
        meta={metaTags}
      />
      {children}
    </>
  );
};
