import React, {useState, useEffect, useCallback} from "react";
import {useDispatch} from "react-redux";
import {UserLayout} from "src/app/components/UserLayout";
import {makeStyles} from "@material-ui/core/styles";
import {Waypoint} from "react-waypoint";
import {t} from "../../../../i18n/utils";
import {shortNum} from "src/util/NumberUtil";
import {Loader} from "../../settings/Loader";
import axios from "axios";
import {useHistory} from "react-router-dom";
import clsx from "clsx";
import {NoDataMessage} from "../../settings/NoDataMessage";
import {changeFollowingStatus} from "src/store/modules/status";
import Global from "src/system/Global";

const useStyles = makeStyles((theme) => ({
  wrapper: {
    height: "100%",
  },
  cardWrapper: {
    height: "auto",
    borderBottom: `1px solid ${theme.palette.grey.A800}`,
    marginBottom: theme.spacing(20),
  },
  followFeedHeader: {
    display: "flex",
    height: theme.spacing(7),
  },
  twitterFollower: {
    width: "100%",
    height: theme.spacing(5.125),
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    background: "#F2F9FF",
    "& span": {
      padding: "0px",
      fontSize: "13px",
      lineHeight: "130%",
      color: "#566F94",
    },
  },
  headerContent: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    flex: 1,
    borderBottom: `1px solid ${theme.input.borderColor}`,
    cursor: "pointer",
    fontSize: 16,
    lineHeight: "16px",
    fontWeight: 600,
    color: theme.palette.text.secondary,
    "& span": {
      opacity: 0.5,
    },
  },
  tabActive: {
    color: theme.palette.primary.main,
    borderColor: theme.palette.primary.main,
    borderWidth: 2,
    opacity: 1,
    "& span": {
      opacity: 1,
    },
  },
}));

export const FollowFeed = ({
  userId,
  currentUserId,
  userInfo,
  content,
  token,
}) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const maxPerPage = 10;
  const history = useHistory();
  const FOLLOWING = "FOLLOWING";
  const FOLLOWERS = "FOLLOWERS";
  const [itemsGroup, setItemsGroup] = useState({});
  const [items, setItems] = useState([]);
  const [itemsExists, setItemsExists] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [hasMore, setHasMore] = useState(true);
  const [pageNumber, setPageNumber] = useState(0);

  const api = Global.GetPortal().getAppService();

  const fetchData = useCallback(async () => {
    const type = content === FOLLOWING ? "followings" : "followers";
    let data = {};
    let dataArr = [];
    setIsLoading(true);

    let xAuth =
      currentUserId === null
        ? `{"user": null, "token": null}`
        : `{"user": "${currentUserId}", "token": "${token}"}`;
    await axios
      .get(
        `${process.env.REACT_APP_API_URL}/u/user/${userId}/${type}/?offset=${pageNumber}&max=${maxPerPage}&incl=userstats|userinfo|followings`,
        {
          headers: {"x-app-auth": xAuth},
        },
      )
      .then((res) => {
        const dataExists = res.data?.result?.aux?.fws;
        data = res.data?.result?.aux?.uinf;
        dataArr = res.data?.result?.data?.list;

        // if (data) {
        //   for (const [key, value] of Object.entries(data)) {
        //     dataArr.push(value);
        //   }
        // }

        if (dataExists) {
          setItemsExists([...itemsExists, ...dataExists]);
          for (const [key, value] of Object.entries(dataExists)) {
            dispatch(
              changeFollowingStatus({
                userId: value,
                status: true,
              }),
            );
          }
        }
      })
      .catch((err) => {
        console.error(err);
      });

    setPageNumber(pageNumber + maxPerPage);

    if (dataArr.length < maxPerPage) {
      setHasMore(false);
    }

    setItemsGroup({...itemsGroup, ...data});
    setItems([...items, ...dataArr]);

    setIsLoading(false);
  });
  const handleRedirect = (page) => {
    page === FOLLOWING
      ? history.replace(`/user/${userId}/following`)
      : history.replace(`/user/${userId}/followers`);
  };

  const removeItem = (id) => {
    if (content === FOLLOWING) {
      const filterItems = [...items].filter((item) => item._id !== id);
      setItems([...filterItems]);
    }
  };

  useEffect(() => {
    items.length <= 0 && fetchData();
  }, []);

  const renderItems = () => {
    if (items) {
      return items.map((username) => {
        const user = itemsGroup[username];
        if (itemsExists.includes(user._id)) {
          user.isFollowing = true;
        } else {
          user.isFollowing = false;
        }
        return <UserLayout userInfo={user} isPage isProfile />;
      });
    }
    return null;
  };

  const getTWTFollowers = () => {
    // let flg = userInfo?.data?.flg ? userInfo?.data?.flg : 0;
    let twitter = userInfo?.data?.twt_flg ? userInfo?.data?.twt_flg : 0;
    return shortNum(twitter * 1);
  };
  return (
    <div className={classes.wrapper}>
      <div className={classes.followFeedHeader}>
        <div
          className={clsx(
            classes.headerContent,
            content === FOLLOWERS ? classes.tabActive : "",
          )}
          onClick={() => handleRedirect(FOLLOWERS)}
        >
          <span>{t("getter_fe.profile.common.followers")}</span>
        </div>
        <div
          className={clsx(
            classes.headerContent,
            content === FOLLOWING ? classes.tabActive : "",
          )}
          onClick={() => handleRedirect(FOLLOWING)}
        >
          <span>{t("getter_fe.profile.common.following")}</span>
        </div>
      </div>
      {getTWTFollowers() === 0 || content === FOLLOWING ? null : (
        <div className={classes.twitterFollower}>
          <span>
            {t("getter_fe.profile.common.twitterFollowers", {
              userId: userId,
              number: getTWTFollowers(),
            })}
          </span>
        </div>
      )}
      {!isLoading &&
        items.length <= 0 &&
        (currentUserId === userId ? (
          <NoDataMessage
            title={
              content === FOLLOWING
                ? t("getter_fe.profile.common.noFollowingTitle")
                : t("getter_fe.profile.common.noFollowersTitle")
            }
            message={
              content === FOLLOWING
                ? t("getter_fe.profile.common.noFollowingDescription")
                : t("getter_fe.profile.common.noFollowersDescription")
            }
          />
        ) : (
          <NoDataMessage
            title={
              content === FOLLOWING
                ? t("getter_fe.profile.common.otherUserNoFollowingTitle")
                : t("getter_fe.profile.common.otherUserNoFollowersTitle")
            }
            message={
              content === FOLLOWING
                ? t("getter_fe.profile.common.otherUserNoFollowingDescription")
                : t("getter_fe.profile.common.otherUserNoFollowersDescription")
            }
          />
        ))}

      <div className={!isLoading && items.length ? classes.cardWrapper : ""}>
        {renderItems()}
      </div>
      {isLoading && <Loader />}
      {!isLoading && hasMore && items.length > 0 && (
        <Waypoint
          scrollableAncestor={window}
          fireOnRapidScroll={false}
          onEnter={fetchData}
          className={classes.wayPoint}
        />
      )}
    </div>
  );
};

export default FollowFeed;
