import React from "react";

import {connect} from "react-redux";
import Popup from "reactjs-popup";
import Util from "src/core/Util";

import {ReactComponent as IconCancel} from "src/assets/icons/icon_close.svg";
import {scrollHelper} from "src/util/scrollUtils";

const connector = connect((state) => {
  return {};
}, {});

export const PopupDialogNew = connector(_PopupDialogNew);

function _PopupDialogNew(props) {
  const {
    open,
    customHeader,
    headerContent,
    showCloseIcon,
    children,
    contentStyle,
    onCancel,
    setCloseFunc,
    onClose,
  } = props;

  let titleComp = Util.IsString(headerContent) ? (
    <div className="title">{headerContent}</div>
  ) : (
    headerContent
  );

  let headerComp = (
    <React.Fragment>
      <div className="header">
        {showCloseIcon && (
          <span className="icon cancel" onClick={onCancel}>
            <IconCancel />
          </span>
        )}
        {titleComp}
      </div>
    </React.Fragment>
  );

  let dialogContent = (
    <div>
      {customHeader || headerComp}

      <div className="content" style={{position: "relative"}}>
        {children}
      </div>
    </div>
  );

  // NOTE: follow example in: https://react-popup.elazizi.com/react-modal/
  // open state doesn't work. But using the close() function passed into
  // the children works!
  let comp = (
    <Popup
      contentStyle={contentStyle}
      open={open}
      closeOnDocumentClick={false}
      closeOnEscape={false}
      modal
      lockScroll={false}
      resetScroll
      position="center center"
      repositionOnResize
      onClose={() => {
        if (typeof onClose === "function") {
          onClose();
        }
        scrollHelper.unlock();
      }}
      {...props}
    >
      {(close) => {
        if (typeof setCloseFunc === "function") {
          setCloseFunc(() => close);
        }

        return dialogContent;
      }}
    </Popup>
  );
  return comp;
}
