import React from "react";

import AppConsts from "../AppConsts";
import {NewUserFollowView} from "../pages/profile/follow/NewUserFollowView";
import {NewUIPage} from "../base/NewUIPage";

const CONTENT = "FOLLOWING";

export const NewUserFollowing = ({match}) => {
  const {id: userId} = match.params;
  const description = `${AppConsts.APP_NAME} user ${userId}`;

  return (
    <NewUIPage title={userId} description={description}>
      <NewUserFollowView userId={userId} content={CONTENT} />
    </NewUIPage>
  );
};
